@font-face {
  font-family: 'MontserratLight';
  src: url('/src/assets/fonts/montserrat/Montserrat-Light.woff2') format('woff2');
  font-weight: 400; /* Adjust as needed */
  font-style: normal;
}



/* General Styles */
body {
  font-family: 'MontserratLight', sans-serif;
  font-size: 16px;
  line-height: 1.6;
  color: #333; /* General text color */
}




h1, h2, h3, h4, h5, h6 {
  font-family: 'MontserratLight', ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Arial, "Apple Color Emoji", "Segoe UI Emoji";
  margin-top: 0;
  letter-spacing: -0.01em; /* tighten display */
}

.caption {
  font-size: 0.875rem; /* 14px */
  color: #6b7280; /* gray-500 */
}

h1 {
  font-size: 4.5rem; /* 72px */
  line-height: 1.1;
}

h2 {
  font-size: 3rem; /* 48px */
  line-height: 1.15;
}

h3 {
  font-size: 1.5rem; /* 24px */
  font-weight: 600;
}

h4 {
  font-size: 1.25rem; /* 20px */
  font-weight: 600;
}

h5 {
  font-size: 1.125rem; /* 18px */
  line-height: 1.5rem;
  font-weight: 600;
}

h6 {
  font-size: 1rem; /* 16px */
  font-weight: 600;
}

p {
  font-size: 1rem; /* 16px */
  line-height: 1.6; /* Adjust line-height for readability */
}


/* Header Styles */
.logo {
  font-size: 36px;
  font-weight: bold;
  color: #000; /* Logo color */
}

.text-size-1-5rem {
  font-size: 1.5rem !important;
}

